'use client';

import { useState, useEffect } from 'react';
import axios from 'axios';

export default function AddProductPage() {
  const [name, setName] = useState('');
  const [price, setPrice] = useState('');
  const [discountPrice, setDiscountPrice] = useState('');
  const [images, setImages] = useState<string[]>([]);
  const [category, setCategory] = useState('');
  const [description, setDescription] = useState('');
  const [ingredients, setIngredients] = useState<string[]>([]);
  const [instructions, setInstructions] = useState<string[]>([]);
  const [specials, setSpecials] = useState<string[]>([]);
  const [stock, setStock] = useState('');
  const [allCategories, setAllCategories] = useState([]);

  useEffect(() => {
    axios.get('https://api-zeal.onrender.com/api/categories')
      .then(res => setAllCategories(res.data))
      .catch(err => console.error('Lỗi lấy danh mục:', err));
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const productData = {
      name,
      price: Number(price),
      discountPrice: discountPrice ? Number(discountPrice) : null,
      images,
      category,
      description,
      ingredients,
      usage_instructions: instructions,
      special: specials,
      stock: Number(stock),
    };

    try {
      const res = await axios.post('https://api-zeal.onrender.com/api/products', productData);
      alert('Thêm sản phẩm thành công!');
    } catch (error: any) {
      alert(`Thêm thất bại: ${error.response?.data?.message || 'Lỗi không xác định'}`);
    }
  };

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      const fileNames = Array.from(files).map(file => file.name);
      setImages(prev => [...prev, ...fileNames]);
    }
  };

  return (
    <form onSubmit={handleSubmit} style={{ width: '100%', maxWidth: '800px', margin: '0 auto' }}>
      <h1>Thêm sản phẩm</h1>

      <input placeholder="Tên sản phẩm" value={name} onChange={e => setName(e.target.value)} required />
      <input type="number" placeholder="Giá" value={price} onChange={e => setPrice(e.target.value)} required />
      <input type="number" placeholder="Giá khuyến mãi (nếu có)" value={discountPrice} onChange={e => setDiscountPrice(e.target.value)} />
      <input type="number" placeholder="Tồn kho" value={stock} onChange={e => setStock(e.target.value)} required />

      <select value={category} onChange={e => setCategory(e.target.value)} required>
        <option value="">-- Chọn danh mục --</option>
        {allCategories.map((c: any) => (
          <option key={c._id} value={c._id}>{c.name}</option>
        ))}
      </select>

      <textarea placeholder="Mô tả" value={description} onChange={e => setDescription(e.target.value)} required />

      <textarea placeholder="Thành phần (cách nhau bởi dấu xuống dòng)" value={ingredients.join('\n')} onChange={e => setIngredients(e.target.value.split('\n'))} />
      <textarea placeholder="Hướng dẫn sử dụng (cách nhau bởi dấu xuống dòng)" value={instructions.join('\n')} onChange={e => setInstructions(e.target.value.split('\n'))} />
      <textarea placeholder="Đặc điểm nổi bật (cách nhau bởi dấu xuống dòng)" value={specials.join('\n')} onChange={e => setSpecials(e.target.value.split('\n'))} />

      <input type="file" multiple onChange={handleImageUpload} />
      {images.map((img, index) => (
        <p key={index}>{img}</p>
      ))}

      <button type="submit">Thêm sản phẩm</button>
    </form>
  );
}
